'use client'

import { useState, useEffect } from 'react'
import { Stop } from '@/types/transport'
import { X } from 'lucide-react'

interface StopFormModalProps {
  stop: Stop | null
  onSave: (stop: Omit<Stop, 'id'>) => void
  onClose: () => void
}

export default function StopFormModal({
  stop,
  onSave,
  onClose,
}: StopFormModalProps) {
  const [formData, setFormData] = useState({
    name: '',
    code: '',
    address: '',
    latitude: 0,
    longitude: 0,
    accessible: false,
    status: 'active' as const,
  })

  const [errors, setErrors] = useState<Record<string, string>>({})

  useEffect(() => {
    if (stop) {
      setFormData({
        name: stop.name,
        code: stop.code,
        address: stop.address,
        latitude: stop.latitude,
        longitude: stop.longitude,
        accessible: stop.accessible,
        status: stop.status,
      })
    }
  }, [stop])

  const validateForm = () => {
    const newErrors: Record<string, string> = {}

    if (!formData.name.trim()) newErrors.name = 'Le nom est requis'
    if (!formData.code.trim()) newErrors.code = 'Le code est requis'
    if (!formData.address.trim()) newErrors.address = "L'adresse est requise"
    if (formData.latitude < -90 || formData.latitude > 90) {
      newErrors.latitude = 'Latitude invalide (-90 à 90)'
    }
    if (formData.longitude < -180 || formData.longitude > 180) {
      newErrors.longitude = 'Longitude invalide (-180 à 180)'
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    if (!validateForm()) return

    onSave({
      name: formData.name,
      code: formData.code,
      address: formData.address,
      latitude: formData.latitude,
      longitude: formData.longitude,
      accessible: formData.accessible,
      status: formData.status,
    })
  }

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg max-w-2xl w-full max-h-[90vh] overflow-y-auto">
        {/* Header */}
        <div className="flex justify-between items-center p-6 border-b border-gray-200 sticky top-0 bg-white">
          <h2 className="text-2xl font-bold text-gray-900">
            {stop ? 'Modifier un Arrêt' : 'Ajouter un Arrêt'}
          </h2>
          <button
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700"
          >
            <X size={24} />
          </button>
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="p-6 space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Nom *
              </label>
              <input
                type="text"
                value={formData.name}
                onChange={(e) =>
                  setFormData(prev => ({ ...prev, name: e.target.value }))
                }
                className={`w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                  errors.name ? 'border-red-500' : 'border-gray-300'
                }`}
                placeholder="Nom de l'arrêt"
              />
              {errors.name && (
                <p className="text-red-500 text-xs mt-1">{errors.name}</p>
              )}
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Code *
              </label>
              <input
                type="text"
                value={formData.code}
                onChange={(e) =>
                  setFormData(prev => ({ ...prev, code: e.target.value }))
                }
                className={`w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                  errors.code ? 'border-red-500' : 'border-gray-300'
                }`}
                placeholder="S001"
              />
              {errors.code && (
                <p className="text-red-500 text-xs mt-1">{errors.code}</p>
              )}
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Adresse *
            </label>
            <input
              type="text"
              value={formData.address}
              onChange={(e) =>
                setFormData(prev => ({ ...prev, address: e.target.value }))
              }
              className={`w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                errors.address ? 'border-red-500' : 'border-gray-300'
              }`}
              placeholder="Adresse complète"
            />
            {errors.address && (
              <p className="text-red-500 text-xs mt-1">{errors.address}</p>
            )}
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Latitude *
              </label>
              <input
                type="number"
                step="0.0001"
                value={formData.latitude}
                onChange={(e) =>
                  setFormData(prev => ({ ...prev, latitude: parseFloat(e.target.value) || 0 }))
                }
                className={`w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                  errors.latitude ? 'border-red-500' : 'border-gray-300'
                }`}
                placeholder="30.4270"
              />
              {errors.latitude && (
                <p className="text-red-500 text-xs mt-1">{errors.latitude}</p>
              )}
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Longitude *
              </label>
              <input
                type="number"
                step="0.0001"
                value={formData.longitude}
                onChange={(e) =>
                  setFormData(prev => ({ ...prev, longitude: parseFloat(e.target.value) || 0 }))
                }
                className={`w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                  errors.longitude ? 'border-red-500' : 'border-gray-300'
                }`}
                placeholder="-9.5981"
              />
              {errors.longitude && (
                <p className="text-red-500 text-xs mt-1">{errors.longitude}</p>
              )}
            </div>
          </div>

          <div className="flex items-center gap-3">
            <input
              type="checkbox"
              id="accessible"
              checked={formData.accessible}
              onChange={(e) =>
                setFormData(prev => ({ ...prev, accessible: e.target.checked }))
              }
              className="w-4 h-4 cursor-pointer"
            />
            <label htmlFor="accessible" className="text-sm font-medium text-gray-700 cursor-pointer">
              Accessible PMR (Personnes à Mobilité Réduite)
            </label>
          </div>

          {/* Buttons */}
          <div className="flex gap-3 pt-4 border-t border-gray-200">
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-4 py-2 border border-gray-300 rounded-lg text-gray-700 font-medium hover:bg-gray-50 transition-colors"
            >
              Annuler
            </button>
            <button
              type="submit"
              className="flex-1 px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg font-medium transition-colors"
            >
              {stop ? 'Mettre à jour' : 'Créer'}
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}
