'use client'

import { Stop } from '@/types/transport'
import { Trash2, Edit2 } from 'lucide-react'

interface StopsTableProps {
  stops: Stop[]
  onEdit: (stop: Stop) => void
  onDelete: (id: string) => void
  onToggle: (id: string) => void
}

export default function StopsTable({
  stops,
  onEdit,
  onDelete,
  onToggle,
}: StopsTableProps) {
  return (
    <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
      <div className="overflow-x-auto">
        <table className="w-full">
          <thead className="bg-gray-50 border-b border-gray-200">
            <tr>
              <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900">
                Nom
              </th>
              <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900">
                Code
              </th>
              <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900">
                Adresse
              </th>
              <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900">
                Coordonnées
              </th>
              <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900">
                PMR
              </th>
              <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900">
                Statut
              </th>
              <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900">
                Actions
              </th>
            </tr>
          </thead>
          <tbody>
            {stops.map((stop, index) => (
              <tr
                key={stop.id}
                className={`border-b border-gray-200 ${
                  index % 2 === 0 ? 'bg-white' : 'bg-gray-50'
                } hover:bg-blue-50 transition-colors`}
              >
                <td className="px-6 py-4 text-sm font-medium text-gray-900">
                  {stop.name}
                </td>
                <td className="px-6 py-4 text-sm text-gray-700">
                  <span className="bg-blue-100 text-blue-800 px-2 py-1 rounded font-mono text-xs">
                    {stop.code}
                  </span>
                </td>
                <td className="px-6 py-4 text-sm text-gray-700">
                  {stop.address}
                </td>
                <td className="px-6 py-4 text-sm text-gray-700">
                  <span className="text-xs bg-gray-100 px-2 py-1 rounded">
                    {stop.latitude.toFixed(4)}, {stop.longitude.toFixed(4)}
                  </span>
                </td>
                <td className="px-6 py-4 text-sm">
                  <span
                    className={`px-2 py-1 rounded text-xs font-medium ${
                      stop.accessible
                        ? 'bg-green-100 text-green-800'
                        : 'bg-gray-100 text-gray-800'
                    }`}
                  >
                    {stop.accessible ? 'Oui' : 'Non'}
                  </span>
                </td>
                <td className="px-6 py-4 text-sm">
                  <span
                    className={`px-2 py-1 rounded text-xs font-semibold ${
                      stop.status === 'active'
                        ? 'bg-green-100 text-green-800'
                        : 'bg-red-100 text-red-800'
                    }`}
                  >
                    {stop.status === 'active' ? 'Actif' : 'Inactif'}
                  </span>
                </td>
                <td className="px-6 py-4 text-sm">
                  <div className="flex gap-2">
                    <button
                      onClick={() => onToggle(stop.id)}
                      className={`px-2 py-1 rounded text-xs font-medium transition-colors ${
                        stop.status === 'active'
                          ? 'bg-orange-100 hover:bg-orange-200 text-orange-800'
                          : 'bg-green-100 hover:bg-green-200 text-green-800'
                      }`}
                    >
                      {stop.status === 'active' ? 'Désactiver' : 'Activer'}
                    </button>
                    <button
                      onClick={() => onEdit(stop)}
                      className="px-2 py-1 rounded bg-blue-100 hover:bg-blue-200 text-blue-800 text-xs font-medium transition-colors"
                    >
                      <Edit2 size={14} />
                    </button>
                    <button
                      onClick={() => onDelete(stop.id)}
                      className="px-2 py-1 rounded bg-red-100 hover:bg-red-200 text-red-800 text-xs font-medium transition-colors"
                    >
                      <Trash2 size={14} />
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      {stops.length === 0 && (
        <div className="text-center py-12">
          <p className="text-gray-500">Aucun arrêt trouvé</p>
        </div>
      )}
    </div>
  )
}
