'use client'

import { useState } from 'react'
import LinesGrid from './lines-grid'
import LineFormModal from './line-form-modal'
import { Line } from '@/types/transport'
import { mockLines } from '@/data/mock-data'

interface LinesPageProps {
  city: string
}

export default function LinesPage({ city }: LinesPageProps) {
  const [lines, setLines] = useState<Line[]>(mockLines.filter(l => l.city === city))
  const [isFormOpen, setIsFormOpen] = useState(false)
  const [editingLine, setEditingLine] = useState<Line | null>(null)

  const handleAddLine = () => {
    setEditingLine(null)
    setIsFormOpen(true)
  }

  const handleEditLine = (line: Line) => {
    setEditingLine(line)
    setIsFormOpen(true)
  }

  const handleSaveLine = (lineData: Omit<Line, 'id'>) => {
    if (editingLine) {
      setLines(lines.map(l => l.id === editingLine.id ? { ...lineData, id: editingLine.id } : l))
    } else {
      const newLine: Line = {
        ...lineData,
        id: `L${String(Math.max(...lines.map(l => parseInt(l.id.slice(1)) || 0), 0) + 1).padStart(2, '0')}`,
      }
      setLines([...lines, newLine])
    }
    setIsFormOpen(false)
  }

  const handleDeleteLine = (id: string) => {
    setLines(lines.filter(l => l.id !== id))
  }

  const handleToggleLine = (id: string) => {
    setLines(lines.map(l =>
      l.id === id ? { ...l, status: l.status === 'active' ? 'inactive' : 'active' } : l
    ))
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h3 className="text-xl font-semibold text-gray-900">
          Lignes de Transport - {city}
        </h3>
        <button
          onClick={handleAddLine}
          className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-2 rounded-lg font-medium transition-colors"
        >
          + Ajouter une Ligne
        </button>
      </div>

      <LinesGrid
        lines={lines}
        onEdit={handleEditLine}
        onDelete={handleDeleteLine}
        onToggle={handleToggleLine}
      />

      {isFormOpen && (
        <LineFormModal
          line={editingLine}
          onSave={handleSaveLine}
          onClose={() => setIsFormOpen(false)}
        />
      )}
    </div>
  )
}
