'use client'

import { Line } from '@/types/transport'
import { Trash2, Edit2 } from 'lucide-react'

interface LinesGridProps {
  lines: Line[]
  onEdit: (line: Line) => void
  onDelete: (id: string) => void
  onToggle: (id: string) => void
}

export default function LinesGrid({
  lines,
  onEdit,
  onDelete,
  onToggle,
}: LinesGridProps) {
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
      {lines.map((line) => (
        <div
          key={line.id}
          className="bg-white shadow-md border border-gray-200 rounded-lg p-4 hover:shadow-lg transition-shadow"
        >
          {/* Header */}
          <div className="flex justify-between items-start mb-3">
            <div>
              <div className="text-3xl font-bold text-blue-600">{line.id}</div>
              <h3 className="text-lg font-semibold text-gray-900 mt-1">
                {line.name}
              </h3>
            </div>
            <div
              className={`px-3 py-1 rounded-full text-xs font-semibold ${
                line.status === 'active'
                  ? 'bg-green-100 text-green-800'
                  : 'bg-red-100 text-red-800'
              }`}
            >
              {line.status === 'active' ? 'Actif' : 'Inactif'}
            </div>
          </div>

          {/* Details */}
          <div className="space-y-2 mb-4 text-sm text-gray-600">
            <div className="flex justify-between">
              <span>Nombre d'arrêts:</span>
              <span className="font-semibold text-gray-800">{line.stops.length}</span>
            </div>
            <div>
              <span className="block text-xs text-gray-500 mb-1">Direction:</span>
              <span className="text-gray-700">{line.direction}</span>
            </div>
          </div>

          {/* Actions */}
          <div className="flex gap-2">
            <button
              onClick={() => onToggle(line.id)}
              className={`flex-1 px-3 py-2 rounded-lg font-medium text-sm transition-colors ${
                line.status === 'active'
                  ? 'bg-orange-100 hover:bg-orange-200 text-orange-800'
                  : 'bg-green-100 hover:bg-green-200 text-green-800'
              }`}
            >
              {line.status === 'active' ? 'Désactiver' : 'Activer'}
            </button>
            <button
              onClick={() => onEdit(line)}
              className="px-3 py-2 rounded-lg bg-blue-100 hover:bg-blue-200 text-blue-800 font-medium text-sm transition-colors flex items-center gap-1"
            >
              <Edit2 size={14} />
              Modifier
            </button>
            <button
              onClick={() => onDelete(line.id)}
              className="px-3 py-2 rounded-lg bg-red-100 hover:bg-red-200 text-red-800 font-medium text-sm transition-colors flex items-center gap-1"
            >
              <Trash2 size={14} />
            </button>
          </div>
        </div>
      ))}
    </div>
  )
}
