'use client'

interface HeaderProps {
  selectedCity: string
  onCityChange: (city: string) => void
  cities: string[]
  currentPage: string
}

export default function Header({
  selectedCity,
  onCityChange,
  cities,
  currentPage,
}: HeaderProps) {
  const getPageTitle = () => {
    switch (currentPage) {
      case 'lines':
        return 'Gestion des Lignes'
      case 'stops':
        return 'Gestion des Arrêts'
      default:
        return 'Dashboard'
    }
  }

  return (
    <header className="bg-white border-b border-gray-200 px-6 py-4 flex items-center justify-between shadow-sm">
      <h2 className="text-2xl font-bold text-gray-800">{getPageTitle()}</h2>
      
      <div className="flex items-center gap-4">
        <label className="flex items-center gap-2">
          <span className="text-sm font-medium text-gray-700">Ville:</span>
          <select
            value={selectedCity}
            onChange={(e) => onCityChange(e.target.value)}
            className="px-3 py-2 border border-gray-300 rounded-lg bg-white text-gray-800 font-medium focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            {cities.map((city) => (
              <option key={city} value={city}>
                {city}
              </option>
            ))}
          </select>
        </label>
      </div>
    </header>
  )
}
